<template>
    <div class="post-votes" :aria-label="posts.votes_sum + ':   ' + [posts.upvotes === 0 ? '0' : posts.upvotes ] + '  ' + [posts.downvotes === 0 ? '0' :posts.downvotes]" data-balloon-pos="left">
        <span class="mdi mdi-thumb-up-outline" :class="{upvoted: posts.upvoted}"
              @click="upvote(posts.id)"/>
        <span :style="{color: colorStatus}" class="rating">
            {{ posts.votes }}
        </span>
        <span class="mdi mdi-thumb-down-outline" :class="{downvoted: posts.downvoted}"
              @click="downvote(posts.id)"/>
    </div>
</template>

<script>
    export default {
        props: ['posts', 'auth_check'],
        data: function () {
            return {
                post: [],
                colorStatus: 'inherit',
                upvoted: false,
                downvoted: false,
                vote_status: true,
            }
        },
        async created () {
            if (this.posts.votes > 0) {
                this.colorStatus = 'var(--color-primary-main)';
            } else if (this.posts.votes === 0) {
                this.colorStatus = 'inherit';
            } else if (this.posts.votes < 0) {
                this.colorStatus = 'var(--color-error-main)';
            }
            if (this.auth_check === 1) {
                this.vote_status = false;
            }
        },
        methods: {
            upvote: function (id) {
                if (this.auth_check) {
                    let vote = this.posts.votes;
                    let votes_sum = this.posts.votes_sum;
                    let upvotes = this.posts.upvotes;
                    let downvotes = this.posts.downvotes;
                    console.log(this.posts.upvoted)
                    if (this.posts.upvoted == false && this.posts.downvoted == false) {
                        this.posts.upvoted = true;
                        upvotes++;
                        votes_sum++;
                        vote++;
                        axios.post('/upvote', {
                            id: id,
                            vote: vote,
                            change_rating: '1',
                            status: 'upvote',
                        })
                            .catch(error => {
                                console.log(error);
                            });
                        new Noty({
                            type: 'success',
                            text: '<div class="notification-image"><i class="mdi mdi-thumb-up-outline"></i></div> Ss verildi',
                        }).show();
                    } else if (this.posts.upvoted == true && this.posts.downvoted == false) {
                        this.posts.upvoted = false;
                        upvotes--;
                        votes_sum--;
                        vote--;
                        axios.post('/upvote', {
                            id: id,
                            vote: vote,
                            change_rating: '-1',
                            status: null
                        })
                            .catch(error => {
                                console.log(error);
                            });
                        new Noty({
                            type: 'error',
                            text: '<div class="notification-image" style="color: #ea5f6d"><i class="mdi mdi-minus-box-outline"></i></div> Ss lv olundu',
                        }).show();
                    } else if (this.posts.upvoted == false && this.posts.downvoted == true) {
                        this.posts.upvoted = true;
                        upvotes = upvotes + 1;
                        downvotes = downvotes - 1;
                        vote = vote + 2;
                        votes_sum = votes_sum + 2;
                        axios.post('/upvote', {
                            id: id,
                            vote: vote,
                            change_rating: '+2',
                            status: 'upvote',
                        })
                            .catch(error => {
                                console.log(error);
                            });
                        new Noty({
                            type: 'success',
                            text: '<div class="notification-image"><i class="mdi mdi-thumb-up-outline"></i></div> Ss verildi',
                        }).show();
                    }

                    if (vote > 0) {
                        this.colorStatus = 'var(--color-primary-main)';
                    } else if (vote == 0) {
                        this.colorStatus = 'inherit';
                    } else if (vote < 0) {
                        this.colorStatus = 'var(--color-error-main)';
                    }
                    this.posts.downvoted = false;
                    this.posts.upvotes = upvotes;
                    this.posts.downvotes = downvotes;
                    this.posts.votes = vote;
                    this.posts.votes_sum = votes_sum;
                } else
                    new Noty({
                        type: 'success',
                        text: '<div class="notification-image"><i class="mdi mdi-account-box"></i></div> ' +
                            '<div class="text">Bu funksiyan istifad etmk n ' +
                            '<a href="/register" class="notification-link">qeydiyyatdan kein</a></div>',
                    }).show();
            },
            downvote: function (id) {
                if (this.auth_check) {
                    let vote = this.posts.votes;
                    let votes_sum = this.posts.votes_sum;
                    let upvotes = this.posts.upvotes;
                    let downvotes = this.posts.downvotes;
                    if (this.posts.downvoted == false && this.posts.upvoted == false) {
                        this.posts.downvoted = true;
                        downvotes++;
                        votes_sum--;
                        vote--;
                        axios.post('/upvote', {
                            id: id,
                            vote: vote,
                            change_rating: '-1',
                            status: 'downvote'
                        })
                            .catch(error => {
                                console.log(error);
                            });
                        new Noty({
                            type: 'success',
                            text: '<div class="notification-image"><i class="mdi mdi-thumb-down-outline"></i></div> Ss verildi',
                        }).show();
                    } else if (this.posts.downvoted == true && this.posts.upvoted == false) {
                        this.posts.downvoted = false;
                        downvotes--;
                        votes_sum++;
                        vote++;
                        axios.post('/upvote', {
                            id: id,
                            vote: vote,
                            change_rating: '1',
                            status: null
                        })
                            .catch(error => {
                                console.log(error);
                            });
                        new Noty({
                            type: 'error',
                            text: '<div class="notification-image" style="color: #ea5f6d"><i class="mdi mdi-minus-box-outline"></i></div> Ss lv olundu',
                        }).show();
                    } else if (this.posts.downvoted == false && this.posts.upvoted == true) {
                        this.posts.downvoted = true;
                        upvotes = upvotes - 1;
                        downvotes = downvotes + 1;
                        vote = vote - 2;
                        votes_sum = votes_sum - 2;
                        axios.post('/upvote', {
                            id: id,
                            vote: vote,
                            change_rating: '-2',
                            status: 'downvote'
                        })
                            .catch(error => {
                                console.log(error);
                            });
                        new Noty({
                            type: 'success',
                            text: '<div class="notification-image"><i class="mdi mdi-thumb-down-outline"></i></div> Ss verildi',
                        }).show();
                    }
                    if (vote > 0) {
                        this.colorStatus = 'var(--color-primary-main)';
                    } else if (vote === 0) {
                        this.colorStatus = 'inherit';
                    } else if (vote < 0) {
                        this.colorStatus = 'var(--color-error-main)';
                    }
                    this.posts.upvoted = false;
                    this.posts.upvotes = upvotes;
                    this.posts.downvotes = downvotes;
                    this.posts.votes = vote;
                    this.posts.votes_sum = votes_sum;
                } else
                    new Noty({
                        type: 'success',
                        text: '<div class="notification-image"><i class="mdi mdi-account-box"></i></div> ' +
                            '<div class="text">Bu funksiyan istifad etmk n ' +
                            '<a href="/register" class="notification-link">qeydiyyatdan kein</a></div>',
                    }).show();
            },
        }
    }
</script>
