<template>
    <div class="hub_follow">
        <span @click="follow(following.id)" v-if="!following.follower && !loading" class="btn btn-primary">
            <i class="mdi mdi-account-plus"></i> zlmk
        </span>
        <span @click="follow(following.id)" v-if="following.follower && !loading" class="btn btn-outline">
            <i class="mdi mdi-check"></i> zlyirsiniz
        </span>
        <span v-if="loading" class="btn btn-outline"><i class="mdi mdi-loading mdi-spin"></i> Gzlyin</span>
    </div>
</template>

<script>
    export default {
        props: ['id', 'auth_check', 'user', 'auth_user', 'follow_check'],
        data: function () {
            return {
                following: this.user,
                loading: false,
            }
        },
        async created() {
            if (this.auth_check){
                this.following['follower'] = this.follow_check
            } else {
                this.following['follower'] = false
            }
        },
        methods: {
            follow: function (id) {
                if (this.auth_check) {
                    this.loading = true;
                    axios.post('/users/' + id + '/follow', {
                        id: id,
                    })
                        .then(response => {
                            if (response.data.success) {
                                new Noty({
                                    type: 'success',
                                    text: '<div class="notification-image">' +
                                        '<i class="mdi mdi-account-check-outline"></i>' +
                                        '</div> ' +
                                        '<div class="text">zlyirsiniz</div>',
                                }).show();
                                this.following.follower = true;
                            } else if (response.data.delete) {
                                new Noty({
                                    type: 'error',
                                    text: '<div class="notification-image">' +
                                        '<i class="mdi mdi-account-remove-outline"></i>' +
                                        '</div> ' +
                                        '<div class="text">zlmirsiz</div>',
                                }).show();
                                this.following.follower = false;
                            }
                            this.loading = false;
                        })
                        .catch(error => {
                            console.log(error);
                            new Noty({
                                type: 'error',
                                text: '<div class="notification-image"><span class="mdi mdi-x"/></div> Xta',
                            }).show();
                            this.loading = false;
                        });
                } else
                    new Noty({
                        type: 'success',
                        text: '<div class="notification-image"><i class="mdi mdi-account-box"></i></div> ' +
                            '<div class="text">Bu funksiyan istifad etmk n ' +
                            '<a href="/register" class="notification-link">qeydiyyatdan kein</a></div>',
                    }).show();
            }
        }
    }
</script>

<style>
    .mdi-check {
        color: var(--color-info-main);
    }
</style>
